<template>
  <form action="https://formspree.io/mrglyebv" method="POST" class="application round3">
    <h3>Оставить заявку</h3>
    <div class="form">
      <div>
        <label>Как к вам обращаться?</label>
        <input type="text" name="name" required>
        <label>Номер телефона</label>
        <input type="tel" name="phone" pattern="+7 ([0-9]{3}) [0-9]{3}-[0-9]{2}-[0-9]{2}" v-model="phone" :required="phonerequired">
        <label>Эл.почта</label>
        <input type="text" name="_replyto" v-model="mail" :required="mailrequired">
      </div>
      <div>
        <label>Комментарий</label>
        <textarea name="Comment" rows="5" cols="21" v-model="comment"></textarea>
      </div>
    </div>
    <div class="form">
      <label id="consent">
        Нажимая на кнопку "Отправить", вы даете&#160;
        <a href="/">согласие на обработку своих персональных данных</a>.
      </label>
      <input class="btn" type="submit" value="Отправить">
    </div>    
    <a href="./#application" alt="Application" title="application">Оставить заявку</a>
  </form>
</template>

<script>
export default {
  name: 'Application',
  data(){ return {
    comment: '',
    mail: '',
    phone: ''
  }},
  computed: {
    mailrequired: function() {
      if(this.phone) return false
      else return true
    },
    phonerequired: function() {
      if(this.mail) return false
      else return true
    }
  },
  methods: { 
    attachData: function(Data) {
      this.comment=this.comment.split("\n____________")
      if( this.comment.length>1 )this.comment.pop()
      this.comment += '\n' + Data
    }
  }
}
</script>

<style scoped>
.application {
  flex-basis: 0;
  background: linear-gradient(150deg, rgb(171, 233, 214), rgb(154, 200, 207));
  margin: 40px auto;
  padding: 10px;
  transition: background-color 0.2s ease-out;
}
#inbanner {
  margin: 40px 0 20px 40px;
}

.form {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}


form>*:last-child {
  display: none;
}


@media screen and (max-width: 1000px) {
  #inbanner {
    margin: 40px auto;
  }
}
@media screen and (max-width: 600px) {

  #inbanner {
    width: 220px;
    height: 60px;
    padding: 0;
    border-radius: 2px;
  }
  #inbanner:hover {
    background: #abe9d6;
  }
  #inbanner:active {
    background: #9ac8cf;
  }
  
  #inbanner>* {
    display: none;
  }
  
  #inbanner>*:last-child {
    display: block;
    height: 100%;
    width: 100%;
  }
}
</style>